import React from 'react';
import type { NextPage } from 'next';
import Profile from '../Profile';

const Collectibles: NextPage = () => {
  return (
    <div
      className="flex-1 h-[100vh] overflow-y-auto flex flex-col p-[24px] pb-[96px] md:p-[64px] box-border items-start justify-start gap-[30px] md:gap-[64px]"
      style={{ fontFamily: 'RocGrotesk-Regular' }}
    >
      <div className="self-stretch flex flex-col md:flex-row items-start justify-start gap-[20px] md:gap-[64px]">
        <div className="flex flex-row items-center w-full md:w-fit md:order-last justify-start gap-[32px] text-lg">
          <Profile />
        </div>
        <div className="flex-1 flex flex-col items-start justify-start gap-[8px]">
          <div className="self-stretch relative tracking-[-0.01em] leading-[30px] md:leading-[60px] text-[30px] md:text-[48px] font-bold text-gray-2">
            Digital Collectibles
          </div>
        </div>
      </div>
      <div className="max-w-[840px] text-gray-1  text-[20px] leading-[30px]">
        {`Imagine a digital collectible as the `}
        <span className="text-gray-2">
          modern-day equivalent of a vinyl record, a cherished artwork, or a
          coveted autograph
        </span>
        {` - an exclusive and entertaining way for your fans to show their support for your work!`}
        <br />
        <br />
        {`With MINTCHIP, you'll soon be able to mint your own digital collectibles
        and even share your earnings with collaborators.`}
        <br />
        <br />
        <div className="text-gray-2">
          In the meantime - add your existing collectibles to your website like
          this:
        </div>
        <br />
        <ul className="list-disc ml-[10px]">
          <li>
            <span className="text-gray-2">Step 1:</span>
            {` Go to the builder`}
          </li>
          <li>
            <span className="text-gray-2">Step 2:</span>
            {` Drag a "digital collectible" block into your website and click to edit`}
          </li>
          <li>
            <span className="text-gray-2">Step 3:</span>
            {` Either connect your wallet (to pull your collectibles) or enter your collectibleâ€™s contract address`}
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Collectibles;
