import React from 'react';
import type { NextPage } from 'next';
import Collectibles from '../../components/dashboardContent/collectibles';
import DashboardLayout from '../../components/DashboardLayout';

const DashboardCollectibles: NextPage = () => {
  return (
    <DashboardLayout page={'collectibles'}>
      <Collectibles />
    </DashboardLayout>
  );
};

export default DashboardCollectibles;
